import { Directive, Input, DoCheck, ElementRef, Output, EventEmitter } from '@angular/core';

@Directive({
  selector: '[tab-content]'
})
export class TabContentDirective implements DoCheck {
  @Input() set 'tab-content'(nm:string) { 
    this.name = nm;
  };
  @Input() set tab(state:any) { 
    this.state = state; 
  };
  constructor(private elRef: ElementRef) {
    this.ele = this.elRef.nativeElement;
   }
  state:any = {};
  name:string;
  private ele:any;
  ngDoCheck() {
    this.state[this.name] ? this.ele.classList.add('active') : this.ele.classList.remove('active');
  }
} 



