import { Component, OnInit, ViewChild, ComponentFactoryResolver, DoCheck, OnDestroy } from '@angular/core';
import { DialogHost } from './dialog-host.directive';
import { UtilService } from '../../services/util.service';

export abstract class DialogContent {
  abstract data:any = {};
}

export const DIALOGACTIONS = {
  ok: 'ok',
  no: 'no',
  cancel: 'cancel'
};

@Component({
  selector: 'vs-dialog',
  templateUrl: './dialog.component.html',
  styleUrls: ['./dialog.component.scss']
})
export class DialogComponent implements OnInit, OnDestroy, DoCheck {
  @ViewChild(DialogHost) dialogHost: DialogHost;
  constructor(private componentFactoryResolver: ComponentFactoryResolver,
              public u: UtilService) {
    this.context = this.u.dialogData;
    const dh = this.dialogHost;
  }
  action = DIALOGACTIONS;
  context: any = {};
  private state = {
    loading:false, 
    loaded:false
  };
  loadComponent() {
    this.state.loading = true;
    setTimeout(() => {
      let componentFactory = this.componentFactoryResolver.resolveComponentFactory(this.context.component);
      let viewContainerRef = this.dialogHost.viewContainerRef;
      viewContainerRef.clear();

      let componentRef = viewContainerRef.createComponent(componentFactory);
      (<DialogContent>componentRef.instance).data = this.context.data;
      this.state.loading = false;
      this.state.loaded = true;
    });
  }
  ngOnInit() {
  }
  ngDoCheck() {
    if (!!this.context.component && !this.state.loading && !this.state.loaded) {
      this.loadComponent();
    }
  }
  doAction(e: any, action?: string) {
    console.log('CLICK ON %s', e.target.className);
    if (!e.target.classList.contains('dialog-backdrop')) {
      switch(action) {
        case DIALOGACTIONS.ok: 
          this.context.callback(this.context.data);
          break;
        case DIALOGACTIONS.cancel:
        case DIALOGACTIONS.no:
          break;
        default: return;
      }
    }
    this.state.loaded = false;
    this.state.loading = false;
    this.u.closeDialog();
  }
  ngOnDestroy() {
    this.state.loaded = false;
    this.state.loading = false;
  }
}



