import { Injectable } from '@angular/core';
import {Router, ActivatedRouteSnapshot, CanActivate, RouterStateSnapshot, CanDeactivate} from '@angular/router';
import {Observable} from 'rxjs/Observable';
import {HttpClient} from '@angular/common/http';
import {environment} from '../../environments/environment';
import { UtilService } from './util.service';
import * as _ from 'lodash';

const CONSTANTS = {
  secret: 'VIRTUAL-SERVICE-EDITOR'
};

export enum LOGINRESPCODE {
  ok = 'ok',        // utenza riconosciuta
  new = 'new',      // utenza creata
  none = 'none',    // utente non registrato
  idle = 'idle'     // utente sospeso (mail inviata in attesa di accettazione)
}

@Injectable()
export class AuthService {

  constructor(private http: HttpClient,
              private u: UtilService) { }
  authenticated = false;
  user: any = null;
  private checkUser() {
    return new Promise((res, rej) => {
      const url = this.getUrl('user/me');
      this.http.get(url).subscribe(user => {
        this.user = user;
        res();
      },  err => {
        this.logout();
        rej(err);
      });
    });
  }
  getToken() {
    return this.u.storage.get('token');
  }
  getAuthorizationHeader() {
    return this.authenticated ? `Bearer ${this.getToken()}` : `Basic ${btoa(CONSTANTS.secret)}`;
  }
  getUrl(path: string) {
    return (environment.origin || '') + path;
  }
  createUser(credentials: any) {
    const data = _.pick(credentials, ['email', 'password']);
    return this.http.post(this.getUrl('user'), data).toPromise();
  }
  login(credentials:any) {
    return new Promise((res, rej) => {
      const data = _.pick(credentials, ['email', 'password']);
      this.http
        .post(this.getUrl('auth/local'), data)
        .subscribe((resp: any) => {
          this.authenticated = !!resp.token;
          this.u.storage.set('token', resp.token);
          switch(resp.code) {
            case LOGINRESPCODE.ok: return this.checkUser().then(() => res(resp), rej);
            default: res(resp);
          }
        }, rej);
    });
  }
  logout() {
    this.u.storage.remove('token');
    this.authenticated = false;
  }
  checkAuth() {
    return new Promise((res, rej) => {
      const token = this.getToken();
      this.authenticated = !!token;
      return token ? this.checkUser().then(res, rej) : rej();
    });
  }
}

// da usare per le rotte sotto autenticazione
@Injectable()
export class CanActivateAuth implements CanActivate {
  constructor(private auth: AuthService) {}
  canActivate(
    route: ActivatedRouteSnapshot,
    state: RouterStateSnapshot
  ): Observable<boolean>|Promise<boolean>|boolean {
    return this.auth.authenticated;
  }
}


