import { Component, OnInit, Input } from '@angular/core';
import { ToolbarService } from '../../services/toolbar.service';
import {  Router, NavigationStart } from '@angular/router';

@Component({
  selector: 'toolbar-header',
  templateUrl: './toolbar-header.component.html',
  styleUrls: ['./toolbar-header.component.scss']
})
export class ToolbarHeaderComponent implements OnInit {
  // @Input() collapsed: boolean;
  constructor(public service: ToolbarService,
              private router: Router) { 
    router.events.forEach((e) => {
      if (e instanceof NavigationStart) {
        this.collapsed = /^\/(editor|monitor)\//gm.test(e.url);
      }
    });
  }
  collapsed = false;
  ngOnInit() {
  }

}



