import { Component, OnInit, OnDestroy } from '@angular/core';
import { ToolbarService } from '../../services/toolbar.service';
import { DocumentService } from '../../services/document.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-monitor',
  templateUrl: './monitor.component.html',
  styleUrls: ['./monitor.component.scss'],
  host: { 'class': 'flex-host' }
})
export class MonitorComponent implements OnInit, OnDestroy {

  constructor(private toolbar: ToolbarService,
              private router: Router,
              public manager: DocumentService) { }
  logItems: any[] = [];
  private interval;
  detail: any = null;
  detail_str: string = null;
  ngOnInit() {
    this.toolbar.register({
      buttons: [{
        divider: true
      }, {
        icon: 'create',
        tooltip: 'edt service',
        click: (e) => this.router.navigate(['editor/' + (this.manager.current || {})._id])
      }, {
        icon: 'view_module',
        tooltip: 'services list',
        click: (e) => this.router.navigate(['list'])
      }, {
        icon: 'power_settings_new',
        tooltip: 'logout',
        click: (e) => this.manager.logout()
      }]
    });
    this.interval = setInterval(() => this.refresh(), 2000);
  }
  ngOnDestroy() {
    this.toolbar.unregister();
    clearInterval(this.interval);
  }
  refresh() {
    const last = this.logItems.length > 0 ? this.logItems[0].time : 0;
    this.manager.getLog(last).then((items: any[]) => {
      items.sort((i1, i2) => i2.time - i1.time).forEach(i => {
        i._time = new Date(i.time);
        i._time_str = i._time.toLocaleTimeString();
      });
      this.logItems = items.concat(this.logItems);
    });
  }
  setDetail(item?: any) {
    this.detail = item;
    this.detail_str = item ? JSON.stringify(item, null, 2) : null;
  }
  closeDetail() {
    this.setDetail();
  }
}



