import { Component, OnInit } from '@angular/core';
import { UtilService } from '../../services/util.service';
import { Router } from '@angular/router';
import { AuthService, LOGINRESPCODE } from '../../services/auth.service';
import { AppComponent } from '../../app.component';


const CODEMESSAGE = {
  none: '',
  new:  'A message has been sent to the email address you entered,\nfollow the instructions to access the application.',
  idle: 'A message has already been sent to the entered email address.\n' +
        'The link in the email is still valid, follow the path to access the application.'
};

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.scss'],
  host: { 'class': 'flex-host' }
})
export class LoginComponent implements OnInit {

  constructor(private u: UtilService,
              private router: Router,
              private appp: AppComponent,
              private auth: AuthService) { }
  private constants = {
    // esempio: almeno una cifra, un carattere minuscolo ed uno maiuscolo,
    // per una lunghezza minima di 8 caratteri
    // (ammessi cifre, caratteri a-z maiuscoli e minuscoli, "_", "-")
    // pswdRgx: '^(?=.*\\d)(?=.*[a-z])(?=.*[A-Z])[0-9a-zA-Z_\\-]{8,}$',
    // pswdRgx: '^[0-9a-zA-Z_\\-]{8,}$',
    pswdRgx: '^[0-9a-zA-Z_\\-]{2,}$',
    userRgx: '^[0-9a-zA-Z_\\-\\.@]{2,}$',
    emailRgx: '^[a-zA-Z0-9.!#$%&\'*+/=?^_`{|}~-]+@[a-zA-Z0-9](?:[a-zA-Z0-9-]{0,61}[a-zA-Z0-9])?(?:\\.[a-zA-Z0-9](?:[a-zA-Z0-9-]{0,61}[a-zA-Z0-9])?)*$'
  };
  credentials:any = {
    email: '',
    password: ''
  };
  valid = false;
  error: any = {
    title: '',
    message: ''
  };
  signing = false;
  private setError(err, title = null) {
    this.error.message = UtilService.getErrorMessage(err);
    this.error.title = title;
  }
  private handleErr(err: any) {
    this.setError(err);
    this.u.idle.stop();
  }
  ngOnInit() {
    this.u.idle.stop();
  }
  validate() {
    const e_rgx = new RegExp(this.constants.emailRgx, 'gi');
    return e_rgx.test(this.credentials.email);
  }
  login(e) {
    if (!this.validate()) { return this.setError('Incorrect email!'); }
    this.u.idle.start('checking credentials...');
    this.auth.login(this.credentials).then((resp: any) => {
      this.u.idle.stop();
      switch (resp.code) {
        case LOGINRESPCODE.ok:
          this.router.navigate(['list']);
          break;
        case LOGINRESPCODE.none:
          this.u.notify(CODEMESSAGE[resp.code]).then(() =>
            this.auth.createUser(this.credentials).then(() =>
              this.u.notify(CODEMESSAGE[LOGINRESPCODE.new]),
              err => this.handleErr(err)));
          break;
        default:
          this.u.notify(CODEMESSAGE[resp.code]);
      }
    }, err => this.handleErr(err));
  }
  toggle() {
    this.appp.toolbar = !this.appp.toolbar;
  }
}



