import { Component, OnInit, ViewChild } from '@angular/core';
import { DialogContent } from '../dialog/dialog.component';

@Component({
  selector: 'app-call-rule-editor',
  templateUrl: './call-rule-editor.component.html',
  styleUrls: ['./call-rule-editor.component.scss']
})
export class CallRuleEditorComponent implements DialogContent, OnInit {
  @ViewChild('ruleeditor') ruleeditor: any;
  constructor() { }
  data: any = {};
  config: any = {
    lineNumbers: true,
    mode: 'javascript',
    gutters: [ 'CodeMirror-linenumbers', 'CodeMirror-foldgutter' ]
  };
  ngOnInit() {
    setTimeout(() => {
      if (!this.ruleeditor.instance) { return; }
      this.ruleeditor.instance.refresh();
      this.ruleeditor.instance.focus();
    });
  }

}



