import { Component, OnInit, ViewChild, ElementRef, HostListener, OnDestroy } from '@angular/core';
import { AuthService } from '../../services/auth.service';
import { UtilService } from '../../services/util.service';
import { Router } from '@angular/router';
import { HttpClient } from '@angular/common/http';
import { CallRuleEditorComponent } from '../../components/call-rule-editor/call-rule-editor.component';
import { DocumentService } from '../../services/document.service';
import { ToolbarService } from '../../services/toolbar.service';
import 'codemirror/mode/javascript/javascript';
import * as _ from 'lodash';

@Component({
  selector: 'app-editor',
  templateUrl: './editor.component.html',
  styleUrls: ['./editor.component.scss'],
  host: { 'class': 'flex-host' }
})
export class EditorComponent implements OnInit, OnDestroy {
  @ViewChild('swagger') swaggerInput: ElementRef;
  @ViewChild('editor_db') dbeditor: any;
  constructor(public auth: AuthService,
              private router: Router,
              private toolbar: ToolbarService,
              private manager: DocumentService,
              public u: UtilService,
              private http: HttpClient) {
    this.currentOrigin = location.origin;
  }
  config: any = {
    lineNumbers: true,
    mode: 'javascript',
    gutters: [ 'CodeMirror-linenumbers', 'CodeMirror-foldgutter' ]
  };
  verbs: any[] = ['get', 'post', 'put', 'patch', 'delete', 'head', 'options', 'connect', 'trace'];
  searchText: string = '';
  calls: any[] = [];
  currentCall: any = null;
  loading = false;
  dragging = false;
  tabState:any = {};
  test: any = {};
  confirm: any = null;
  currentOrigin = '';
  private refreshPathValues() {
    if (!this.currentCall) { return; }
    this.currentCall._pathValues = [];
    ((this.currentCall || {}).path || '').split('/').forEach((part: string) => {
      const m = (/\{(.*?)\}/g).exec(part);
      if (m) { this.currentCall._pathValues.push({name: m[1], value: null}); }
    });
  }
  refreshUrlPath() {
    this.refreshPathValues();
    this.checkChanges();
  }
  @HostListener('drop', ['$event'])
  onDrop(evt: any){
    evt.preventDefault();
    evt.stopPropagation();
    this.dragging = false;
    this.manager.loadSwagger(evt);
  }
  @HostListener('dragenter', ['$event'])
  @HostListener('dragover', ['$event'])
  onDragOver(evt: any) {
    evt.preventDefault();
    evt.stopPropagation();
    this.dragging = true;
  }
  @HostListener('dragleave', ['$event'])
  onDragLeave(evt: any) {
    evt.preventDefault();
    evt.stopPropagation();
    this.dragging = false;
  }
  ngOnInit() {
    this.search();
    this.toolbar.register({
      buttons: [{
        icon: 'add_circle_outline',
        tooltip: 'new call',
        click: (e: any) => this.createCall()
      }, {
        icon: 'save',
        disabled: () => !(this.manager.current || {})._modified,
        tooltip: 'save service',
        color: () => 'accent',
        click: (e: any) => this.manager.save()
      }, {
        divider: true
      }, {
        icon: 'desktop_windows',
        tooltip: 'monitor',
        click: (e: any) => this.router.navigate(['monitor/' + (this.manager.current || {})._id])
      }, {
        icon: 'view_module',
        tooltip: 'services list',
        click: (e: any) => this.router.navigate(['list'])
      }, {
        icon: 'power_settings_new',
        tooltip: 'logout',
        click: (e: any) => this.manager.logout()
      }]
    });
  }
  private clearConfirm() {
    if (this.confirm) { clearTimeout(this.confirm); }
    this.confirm = null;
  }
  ngOnDestroy() {
    this.toolbar.unregister();
    this.clearConfirm();
  }
  logout() {
    this.auth.logout();
    this.router.navigate(['login']);
  }
  search() {
    this.calls = _.filter(((this.manager || {})['current'] || {}).calls || [], (c: any) =>
      this.searchText ? c._url2.indexOf(this.searchText) > -1 : true);
  }
  clearSearch() {
    this.searchText = '';
    this.search();
  }
  changePassword() {
    // TODO...
  }
  openSwagger() {
    this.swaggerInput.nativeElement.click();
  }
  clearFile() {
    this.swaggerInput.nativeElement.value = '';
    this.loading = false;
  }
  onSwaggerChange(e) {
    e.preventDefault();
    e.stopPropagation();
    this.manager.loadSwagger(e)
  }
  checkChanges() {
    this.manager.checkDocChanges();
  }
  selectCall(call?: any) {
    this.clearConfirm();
    this.currentCall = call;
  }
  refresh() {
    setTimeout(() => {
      if (!this.dbeditor.instance) { return; }
      this.dbeditor.instance.refresh();
      this.dbeditor.instance.focus();
    });
  }
  respIcon(type: any) {
    switch (type) {
      case 'file':
        return 'insert_drive_file';
      default:
        return 'code';
    }
  }
  createCall() {
    if (!this.manager.current) { return; }
    this.clearConfirm();
    const call = this.manager.createCall();
    this.search();
    this.selectCall(call);
    this.checkChanges();
  }
  createRule() {
    const rule = {
      code: 500,
      error: '',
      expression: ''
    };
    this.editRule(rule, () => {
      this.currentCall.rules.push(rule);
      this.checkChanges();
    });
  }
  editRule(rule: any, cb?: () => any) {
    this.u.dialog(CallRuleEditorComponent, rule, {title: 'Rule Editor'})
      .then(mrule => {
        _.extend(rule, mrule);
        return cb ? cb() : this.checkChanges();
      });
  }
  deleteRule(rule: any) {
    _.pull(this.currentCall.rules, rule);
    this.checkChanges();
  }
  confirmDeleteCall() {
    this.clearConfirm();
    _.pull(this.manager.current.calls, this.currentCall);
    this.currentCall = null;
    this.checkChanges();
  }
  deleteCall() {
    this.confirm = setTimeout(() => this.confirm = null, 5000);
  }
  setVerb(verb: string) {
    this.currentCall.verb = verb;
    this.checkChanges();
  }
  testCall() {
    /*const vo = {};
    if (this.currentCall._pathValues) this.currentCall._pathValues.forEach((v:any) => vo[v.name] = v.value);
    const path = this.u.format(this.currentCall.path, vo);
    const body = this.u.parseJS(this.currentCall.testData);
    const data = _.isString(body) ? JSON.parse(body) : body;
    const params = (data && ['get', 'delete'].indexOf(this.currentCall.verb) > -1) ? this.u.getUrlParams(data) : null;
    const body_arg = (data && ['post', 'put'].indexOf(this.currentCall.verb) > -1) ? data : null;
    const url = this.u.checkUrl('api/player', this.current.path, path + (params || ''));
    this.doHttp(url, this.currentCall.verb, body_arg || {})
      .subscribe(res => {
        this.test.error = false;
        this.test.result = JSON.stringify(res, null, 2);
      }, err => {
        this.test.error = !!err;
        this.test.result = UtilService.getErrorMessage(err);
      });*/ 
  }
}



