import { Component, OnInit, Input } from '@angular/core';

@Component({
  selector: 'textbox-container',
  templateUrl: './textbox-container.component.html',
  styleUrls: ['./textbox-container.component.scss']
})
export class TextboxContainerComponent implements OnInit {
  @Input('label') set hLabel(l:string) {
    this.label = l;
  }
  constructor() { }
  label: string = null;
  ngOnInit() {
  }

}



