import { Component, Input } from '@angular/core';

@Component({
  selector: 'expression-scope-help',
  template: `<div class="expression-scope-help" layout-col>
    <div *ngIf="!minimal" layout-row><span class="property">data</span><span>request body or params</span></div>
    <div *ngIf="!minimal" layout-row><span class="property">params</span><span>request params</span></div>
    <div *ngIf="!minimal" layout-row><span class="property">db</span><span>virtual database object</span></div>
    <div *ngIf="!minimal" layout-row><span class="property">pathValue</span><span>dynamic path values object</span></div>
    <div *ngIf="!minimal" layout-row><span class="property">headers</span><span>request headers</span></div>
    <div *ngIf="!minimal" layout-row><span class="property">cookies</span><span>session cookies</span></div>
    <div layout-row><span class="property">_</span><span>lodash utilities</span></div>
  </div>`
})
export class ExpressionScopeHelpComponent {
  @Input('minimal') set min(v:boolean) {this.minimal = v;}
  minimal = false;
}


