import { Injectable } from '@angular/core';

export interface IToolbar {
  buttons: any[];
  getTitle?: () => string;
}

class EmptyToolbar implements IToolbar {
  buttons: any[] = [];
  getTitle = () => '';
}

@Injectable()
export class ToolbarService {

  constructor() { }
  private emptyBar = new EmptyToolbar();
  current: IToolbar = this.emptyBar;
  register(bar: IToolbar) {
    this.current = bar || this.emptyBar;
  }
  unregister() {
    this.current = this.emptyBar;
  }
}



