import {BrowserModule} from '@angular/platform-browser';
import {BrowserAnimationsModule} from '@angular/platform-browser/animations';
import {NgModule} from '@angular/core';
import {FormsModule,ReactiveFormsModule} from '@angular/forms';
import {AppComponent} from './app.component';
import {AppRoutingModule} from './app.router';
import {StorageServiceModule} from 'angular-webstorage-service';
import {HttpClientModule,HTTP_INTERCEPTORS} from '@angular/common/http';
import {ClipboardModule} from 'ngx-clipboard';
// SERVICES
import {AuthService,CanActivateAuth} from './services/auth.service';
import {InterceptorService} from './services/interceptor.service';
import {InteractionService} from './services/interaction.service';
import {UtilService} from './services/util.service';
import {DocumentService} from './services/document.service';
import {ToolbarService} from './services/toolbar.service';
// PAGES
import {LoginComponent} from './pages/login/login.component';
import {EditorComponent} from './pages/editor/editor.component';
import {ListComponent} from './pages/list/list.component';
import {MonitorComponent} from './pages/monitor/monitor.component';
// COMPONENTS
import {FlexLayoutModule} from '@angular/flex-layout';
import {TextboxContainerComponent} from './components/textbox-container/textbox-container.component';
import {CodemirrorModule} from 'ng2-codemirror';
import {DialogHost} from './components/dialog/dialog-host.directive'
import {TabButtonDirective} from './components/tab/tab-button.directive';
import {TabContentDirective} from './components/tab/tab-content.directive';
import {ExpressionScopeHelpComponent} from './components/expression-scope-help/expression-scope-help.component';
import {CallRuleEditorComponent} from './components/call-rule-editor/call-rule-editor.component';
import {DialogComponent} from './components/dialog/dialog.component';
import {ToolbarHeaderComponent} from './components/toolbar-header/toolbar-header.component';
// MATERIAL
import {
  MatButtonModule,
  MatToolbarModule,
  MatCheckboxModule,
  MatInputModule,
  MatCardModule,
  MatMenuModule} from '@angular/material';


@NgModule({
  declarations: [
    AppComponent,
    LoginComponent,
    EditorComponent,
    TextboxContainerComponent,
    TabButtonDirective,
    TabContentDirective,
    ExpressionScopeHelpComponent,
    CallRuleEditorComponent,
    DialogHost,
    DialogComponent,
    ToolbarHeaderComponent,
    ListComponent,
    MonitorComponent
  ],
  imports: [
    AppRoutingModule,
    BrowserModule,
    BrowserAnimationsModule,
    FormsModule,
    ReactiveFormsModule,
    FlexLayoutModule,
    StorageServiceModule,
    ClipboardModule,
    HttpClientModule,
    CodemirrorModule,
    MatButtonModule,
    MatCheckboxModule,
    MatToolbarModule,
    MatInputModule,
    MatCardModule,
    MatMenuModule
  ],
  providers: [
    { provide: HTTP_INTERCEPTORS, useClass: InterceptorService, multi: true },
    AuthService,
    CanActivateAuth,
    InteractionService,
    UtilService,
    ToolbarService,
    DocumentService
  ],
  bootstrap: [AppComponent],
  entryComponents: [
    CallRuleEditorComponent
  ]
})
export class AppModule { }



