import { Component, OnInit } from '@angular/core';
import {fadeAnimation} from './app.animations';
import { Router, NavigationEnd, ActivatedRoute } from '@angular/router';
import { AuthService } from './services/auth.service';
import { UtilService } from './services/util.service';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss'],
  animations: [fadeAnimation]
})
export class AppComponent implements OnInit  {
  constructor(private router: Router,
              private auth: AuthService,
              private route: ActivatedRoute,
              public u: UtilService) {
    this.u.idle.start('initializing...');
    // route.data.subscribe((data: any) => this.toolbar = (data || {}).toolbar);
  }
  version = '0.0.1';
  toolbar = false;
  private navigate(target: string) {
    this.u.idle.stop();
    this.router.navigate([target]);
  }
  ngOnInit() {
    this.auth.checkAuth().then(() => this.navigate('list'), (err) => this.navigate('login'));
  }
  getRouterOutletState(outlet:any) {
    return outlet.isActivated ? outlet.activatedRoute : '';
  }
  hideError() {
    this.u.err = null;
  }
}



