import { Injectable } from '@angular/core';
import { UtilService } from './util.service';
import * as _ from 'lodash';
import { AuthService } from './auth.service';
import { HttpClient } from '@angular/common/http';
import { Router } from '@angular/router';
const BASE_PATH = 'service';
const DEFAULT_DBO_SCRIPT = `=
/**
* you can define directly your db object.
* sample:
* {
*   products: [{
*     id: 1,
*     name: 'product 1'
*   }, {
*     id: 2,
*     name: 'product 2'
*   }]
* }
*
* otherwise you can use javascript code to define database object using "=" as the first charcter:
*/
const db = {};
/**
* you can add properties to db object to use them in calls.
* sample:
* db.products = [];
* for(let i=0;i<10;i++) {
*   db.products.push({name:'product n'+(i+1), id:i, ...})
* }
*/
return db;`;

@Injectable()
export class DocumentService {
  constructor(private u: UtilService,
              private auth: AuthService,
              private router: Router,
              private http: HttpClient) { }
  current: any = null;
  idle = false;
  services: any[] = [];
  private load(id: string) {
    if (this.idle) { return; }
    this.idle = true;
    // todo
  }
  private refreshUrls(call?:any, service?:any) {
    service = service || this.current;
    if (!call || !service) { return; }
    call._url = this.u.getUrl(BASE_PATH, service.path || 'undefined', call.path || 'undefined');
    call._url1 = this.u.getUrl(BASE_PATH) + '/';
    call._url2 = this.u.checkUrl(service.path || 'undefined', call.path || 'undefined');
  }
  private getSerialization(o: any) {
    const so = _.omit(o, '_ser', '_original', '_modified', '_confirm');
    return JSON.stringify(so);
  }
  private useSwagger(swg: any) {
    console.log('SWAGGER', swg);
    if (this.current) {
      // TODO: mergia le info del file swagger sul servizio corrente... 
    } else {
      // TODO: genera nuovo servizio da file swagger...
    }
  }
  loadSwagger(e: any) {
    const files = (e.dataTransfer || e.targete).files;
     if (files && files.length > 0) {
       const file = files[0];
       if (!/(.*).json/i.test(file.name)) {
         e.target.value = '';
         this.u.error('Swagger must be a json file!');
       } else {
        this.idle = true;
        const reader = new FileReader();
        reader.onload = () => {
          try {
            const swg = JSON.parse(<string>reader.result);
            this.idle = false;
            this.useSwagger(swg);
          } catch(err) {
            this.idle = false;
            this.u.error(err);
          }
        };
        reader.onerror = (err) => {
          this.idle = false;
          this.u.error(err);
        }
        reader.readAsText(file);
       }
    }
  }
  private doHttp(path: string, method = 'get', data?: any) {
    const url = this.auth.getUrl(path);
    return this.http[method](url, data);
  }
  private reset() {
    this.services = [];
    this.current = null;
  }
  logout() {
    this.reset();
    this.auth.logout();
    this.router.navigate(['login']);
  }
  refreshList(force = false, cb?: () => any) {
    if (force || !(this.services || []).length) {
      this.doHttp('services', 'get').subscribe((resp: any[]) => {
        this.services = resp;
        if (cb) { cb(); }
      }, this.u.error);
    } else if (cb) {
      cb();
    }
  }
  checkDocChanges() {
    setTimeout(() => {
      this.current._ser = this.getSerialization(this.current);
      this.current._modified = this.current._ser !== this.current._original;
    });
  }
  createService() {
    return new Promise((res, rej) => {
      this.doHttp('services', 'post', {name: this.u.getNewName(this.services, 'new virtual-service'), dbo: DEFAULT_DBO_SCRIPT})
        .subscribe(resp => {
          this.refreshList(resp._id, () => {
            this.current = _.find(this.services, s => s._id === resp._id);
            res();
          });
        }, err => {
          this.u.error(err);
          rej();
        });
    });
  }
  private initService(srv: any, full = false) {
    if (!srv) { return; }
    srv.calls = srv.calls || [];
    srv.calls.forEach(c => this.refreshUrls(c));
    if (!!srv._original && !full) { return; }
    srv._original = this.getSerialization(srv);
    srv._modified = false;
  }
  setCurrent(srv: any, route: string = '') {
    this.current = srv;
    this.initService(this.current);
    if (route) { this.router.navigate([route]); }
  }
  createCall() {
    if (!this.current) { return; }
    const path = this.u.getNewName(this.current.calls, 'new-call', 'path');
    const call = {
      verb: 'get',
      path: path,
      respType: 'object',
      rules: [],
      values: [],
      response: '{}',
      testData: ''
    };
    this.current.calls.push(call);
    this.refreshUrls(call);
    this.checkDocChanges();
    return call;
  }
  save() {
    if (!this.current) { return; }
    this.doHttp('services', 'post', this.current)
      .subscribe(() => this.initService(this.current, true), this.u.error);
  }
  deleteService(srv: any) {
    this.doHttp('services/' + srv._id, 'delete').subscribe(
      () => this.refreshList(true), this.u.error);
  }
  deleteCall(call) {
    _.pull(this.current.calls, call);
    this.checkDocChanges();
  }
  checkChanges() {
    // se  modificato chiede se salvare
    return Promise.resolve();
  }
  getLog(last: number) {
    if (!this.current) { return []; }
    const url = 'services/monitor/' + this.current._id + '/' + (last || 0);
    return this.doHttp(url).toPromise();
  }
}



