import { Injectable, Inject } from '@angular/core';
import { LOCAL_STORAGE, WebStorageService } from 'angular-webstorage-service';
import * as _ from 'lodash';

@Injectable()
export class UtilService {

  constructor(@Inject(LOCAL_STORAGE) private local_storage: WebStorageService) {
    this.storage = local_storage;
  }
  storage: WebStorageService = null;
  app: any = {
    name: 'Virtual Service',
    version: '0.0.1'
  };
  err:string = null;
  idle: any = {
    active: false,
    message: '',
    start: (message:string = null) => {
      this.idle.message = message || '';
      setTimeout(() => this.idle.active = true);
    },
    stop: () => setTimeout(() => this.idle.active = false),
    update: (message:string) => this.idle.message = message
  };
  dialogData:any = {
    options: null,
    component: null,
    data: null,
    ok: 'ok',
    cancel: 'cancel',
    valid: true,
    callback: () => {},
    reset: () => {
      this.dialogData.ok = 'ok';
      this.dialogData.cancel = 'cancel';
      this.dialogData.valid = true;
      this.dialogData.component = null;
      this.dialogData.data = null;
      this.dialogData.callback = () => {};
    }
  };
  static isText(txt: string) {
    return !_.startsWith((txt || '').trim(), '<');
  }
  static getErrorMessage(err: any, def?) {
    def = def || 'Generic error!';
    if (_.isString(err)) { return err; }
    if (_.isObject(err)) {
      if (err.error && _.isString(err.error) && UtilService.isText(err.error)) { return err.error; }
      if (_.isObject(err.error) && _.isString(err.error.message)) return err.error.message; 
      if (_.isString(err.message)) { return err.message; }
      if (err.data && _.isString(err.data)) { return err.data; }
      if (_.isObject(err.data)) { return JSON.stringify(err.data); }
      if (err && err.status < 0 && _.isObject(err.config)) {
        return '"' + err.config.method + '" on "' + err.config.url + '" has caused an error';
      }
      if (err.statusText) { return err.status ? err.status + ' - ' + err.statusText : err.statusText; }
    }
    return def;
  }
  copyToClipboard(txt: string) {
    const box = document.createElement('textarea');
    box.style.position = 'fixed';
    box.style.left = '0';
    box.style.top = '0';
    box.style.opacity = '0';
    box.value = txt;
    document.body.appendChild(box);
    box.focus();
    box.select();
    document.execCommand('copy');
    document.body.removeChild(box);
  }
  getNewName(list:any[], template?:string, fieldName?:string) {
    fieldName = fieldName||'name';
    let n = template||'new element';
    if (_.isArray(list)) {
      let index = 0;
      while (!!_.find(list, (i:any) => i[fieldName] === n)) {
        n = template + '(' + (++index) + ')';
      }
    } 
    return n;
  }
  error(err:any) {
    this.err = err ? UtilService.getErrorMessage(err) : null;
    if (err) console.error(err);
  }
  checkUrl(...args: any[]) {
    const url = [];
    (args||[]).forEach(part => {
      part = part || '';
      part = part.replace(/:\/\//, '{VS-PDL}');
      part.split(/\//).forEach((p) => {
        if (p === '..') {
          url.pop();
        } else if (p) {
          url.push(p.replace(/{VS-PDL}/, '://'));
        }
      });
    });
    return url.join('/');
  }
  getUrl(...args: any[]) {
    args.unshift(location.origin);
    return this.checkUrl.apply(null, args);
  }
  private _enc(v) {
    return (v + '');
  }
  getUrlParams(o) {
    const self = this;
    const params_cll = [];
    if (o) {
      _.keys(o).forEach(pn => params_cll.push(self._enc(pn) + '=' + self._enc(o[pn])));
    }
    return params_cll.length ? '?' + params_cll.join('&') : '';
  }
  dialog(component:any, data:any, options: any = {}) {
    return new Promise((res, rej) => {
      this.dialogData.data = data;
      this.dialogData.options = options;
      this.dialogData.callback = (resp:any) => resp ? res(resp) : rej();
      this.dialogData.component = component;
    });
  }
  closeDialog() {
    this.dialogData.reset();
  }
  format(str:string, o:any = {}) {
    str = str || '';
    _.keys(o).forEach(pn => {
      const rgx = new RegExp('{' + pn + '}', 'g');
      str = str.replace(rgx, o[pn] || '');
    });
    return str;
  }
  parseJS(txt: string) {
    const f = new Function('return ' + txt);
    try {
      return f();
    } catch (err) {
      console.error(err);
    }
  }
  notify(message: string) {
    // TODO....
    // mostra messaggio con possibilit di riposta (si no cancel)
    console.log(message);
    return Promise.resolve();
  }
}



