import { Component, OnInit, OnDestroy } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Router } from '@angular/router';
import { AuthService } from '../../services/auth.service';
import { UtilService } from '../../services/util.service';
import { ToolbarService } from '../../services/toolbar.service';
import { DocumentService } from '../../services/document.service';

@Component({
  selector: 'app-list',
  templateUrl: './list.component.html',
  styleUrls: ['./list.component.scss'],
  host: { 'class': 'flex-host' }
})
export class ListComponent implements OnInit, OnDestroy {
  constructor(public auth: AuthService,
              public u: UtilService,
              private router: Router,
              private toolbar: ToolbarService,
              public manager: DocumentService) {
    this.manager.refreshList();
  }
  documents: any[] = [];
  ngOnInit() {
    this.toolbar.register({
      buttons: [{
        icon: 'power_settings_new',
        tooltip: 'logout',
        click: (e) => this.manager.logout()
      }]
    });
  }
  ngOnDestroy() {
    this.toolbar.unregister();
  }
  open(srv: any) {
    this.manager.setCurrent(srv, 'editor/' + srv._id);
  }
  monitor(srv: any) {
    this.manager.setCurrent(srv, 'monitor/' + srv._id);
  }
  download(srv: any) {
    // TODO...
    console.log('Download service', srv);
  }
  confirmDelete(srv: any) {
    clearTimeout(srv._confirm);
    this.manager.deleteService(srv);
  }
  delete(srv: any) {
    srv._confirm = setTimeout(() => srv._confirm = null, 5000);
  }
  newVirtualService() {
    this.manager.createService().then(() => this.router.navigate(['editor/' + this.manager.current._id]));
  }
  importSwagger() {
    // TODO...
    console.log('Import swagger');
  }
}



