import {NgModule} from '@angular/core';
import {RouterModule, Routes} from '@angular/router';
import { LoginComponent } from './pages/login/login.component';
import {CanActivateAuth} from './services/auth.service';
import { EditorComponent } from './pages/editor/editor.component';
import { ListComponent } from './pages/list/list.component';
import { MonitorComponent } from './pages/monitor/monitor.component';

const routes: Routes = [
  { path: '', redirectTo: '/', pathMatch: 'full' },
  { path: 'login', component: LoginComponent },
  { path: 'list', component: ListComponent, canActivate: [CanActivateAuth] },
  { path: 'editor/:id', component: EditorComponent, canActivate: [CanActivateAuth] },
  { path: 'monitor/:id', component: MonitorComponent, canActivate: [CanActivateAuth] }
];

@NgModule({
  imports: [ RouterModule.forRoot(routes, { useHash: true }) ],
  exports: [ RouterModule ],
  providers: []
})
export class AppRoutingModule { }


