import { Directive, ElementRef, HostListener, Input, DoCheck, Output, EventEmitter } from '@angular/core';

@Directive({
  selector: '[tab-button]'
})
export class TabButtonDirective implements DoCheck {
  @Input() set 'tab-button'(nm:string) { 
    this.name = nm;
  };
  @Input() set tab(state:any) { 
    this.state = state;
    setTimeout(() => this.state._selectedTab = this.state._selectedTab || this.name);
  }
  @Output() onSelectTab: EventEmitter<any> = new EventEmitter();
  constructor(private elRef: ElementRef) {
    this.ele = this.elRef.nativeElement;
  }
  name:string;
  state:any = {};
  private ele:any;
  @HostListener('click', ['$event']) 
  onClick(e){
    this.state._selectedTab = this.name;
    this.onSelectTab.emit();
  }
  ngDoCheck() {
    this.state[this.name] = (this.state._selectedTab === this.name);
    this.state[this.name] ? this.ele.classList.add('active') : this.ele.classList.remove('active');
  }
}



